import React, { useEffect, useState, useRef } from 'react';
import Profile from '../../components/profile/Profile';
import { useSelector, useDispatch } from 'react-redux';
import {
  changeField,
  checkExistName,
  check,
  initializeUpdateName,
  updateName,
} from '../../module/redux/user';

const ProfileContainer = () => {
  const dispatch = useDispatch();
  const {
    user,
    form,
    isNotExistName,
    isNotExistNameError,
    updateUser,
    updateUserError,
  } = useSelector(({ user }) => ({
    user: user.user,
    form: user.profile,
    isNotExistName: user.checkExistNameResult,
    isNotExistNameError: user.checkExistNameResultError,
    updateUser: user.updateUser,
    updateUserError: user.updateUserError,
  }));

  const [validName, setValidName] = useState(true); //    
  const [equalName, setEqualName] = useState(true); //     
  const [nameMessage, setNameMessage] = useState(''); //   
  const [submitMessage, setSubmitMessage] = useState(''); // submit  
  const [errorMessage, setErrorMessage] = useState(''); // submit  
  const loadingForm = useRef(true);

  // input  
  const onChange = (e) => {
    setNameMessage('');
    setSubmitMessage('');
    setErrorMessage('');
    const { value, name } = e.target;
    dispatch(changeField({ form: 'profile', key: name, value }));
  };

  const onSubmit = (e) => {
    e.preventDefault();
    const { name } = form;
    const { _id } = user;
    if (validName && !equalName) {
      dispatch(updateName({ id: _id, name }));
    }
  };

  useEffect(() => {
    //    
    if (!isNotExistName || (isNotExistName && isNotExistName.result)) {
      setValidName(true);
    } else {
      //  
      setValidName(false);
      setEqualName(false);
      setNameMessage(isNotExistName ? isNotExistName.message : ''); //  
    }
  }, [isNotExistName]);

  useEffect(() => {
    if (!user) return;
    if (loadingForm.current) return;
    const { name } = form;
    const loginName = user ? user.name : '';
    if (name === undefined) return;
    //     
    if (name && name.toLowerCase() !== loginName.toLowerCase()) {
      //     
      dispatch(checkExistName({ username: user.username, name }));
      setEqualName(false);
    } else {
      //  
      if (name) {
        setEqualName(true); //  
      } else {
        //  
        dispatch(checkExistName({ username: user.username, name })); //       
      }
    }
  }, [dispatch, form, user]);

  useEffect(() => {
    const { name } = user ? user : '';
    dispatch(changeField({ form: 'profile', key: 'name', value: name }));
    loadingForm.current = false;
  }, [dispatch, user]);

  useEffect(() => {
    if (updateUser) {
      dispatch(check());
      setSubmitMessage(' .');
      dispatch(initializeUpdateName());
    } else if (updateUserError) {
      setErrorMessage(' .');
      dispatch(initializeUpdateName());
    }
  }, [dispatch, updateUser, updateUserError]);

  useEffect(() => {
    return () => {
      dispatch(initializeUpdateName());
    };
  }, [dispatch]);

  return (
    <Profile
      user={user}
      form={form}
      onChange={onChange}
      onSubmit={onSubmit}
      validName={validName}
      nameMessage={nameMessage}
      isNotExistNameError={isNotExistNameError}
      equalName={equalName}
      submitMessage={submitMessage}
      errorMessage={errorMessage}
    />
  );
};

export default ProfileContainer;
